.recommended {
  &__products {
    @include pageGrid;
    row-gap: 40px;
  }
  &__product {
    grid-column: 1/-1;
    transition: transform 300ms;

    @include hover(transform, scale(1.1));

    @include onTablet {
      grid-column: 2/-2;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }
}
